html,
body {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  background-color: var(--light) !important;
  font-family: Arial !important;
}

.padding {
  padding: 0.5rem 1rem;
}

.carousel-img {
  width: 100%;
}

.band-col {
  background-color: var(--primary);
}

.figure {
  margin-bottom: 0;
}

.figure-img {
  margin-bottom: 0 !important;
}

.nav-link {
  font-size: 1.6rem !important;
}

.card-pic {
  max-width: 100%;
  max-height: 100%;
  display: block;
}

.footer-hr {
  border-top: 1px solid;
  margin: 0 auto;
}

.footer-txt {
  margin: 0;
  font-size: 1.2rem !important;
  font-weight: 600 !important;
}

#slogan {
  font-size: 13.75px;
}

.navbar-toggler {
  margin-left: auto;
}
